body {
  /* grey theme below thought it was more fun with some colour */
  /* background-image: radial-gradient(circle at center center, #c6c6c6, #354347), repeating-radial-gradient(circle at center center, #c1c1c1, #747474, 26px, transparent 52px, transparent 16px); */
  background-image: radial-gradient(circle at center center, #54bdea6f, #d627c761), repeating-radial-gradient(circle at center center, #c1c1c1, #747474, 26px, transparent 52px, transparent 16px);
  background-blend-mode: multiply;
  background-color: #354347;
  background-repeat: no-repeat;
}

p {
  color: rgb(151, 197, 229);
}
.hoverText{
  font-weight: bold;
  font-style: italic;
  color: rgba(151, 196, 229, 0.456);
}
.pTagDiv:hover{
  
  background: linear-gradient(
    to bottom right,
    rgba(255, 255, 255, 0.233),
    rgba(160, 235, 252, 0.2)
  );
  border: solid rgba(255, 255, 255, 0.015) 5px;
  border-style: outset;
  border-radius: 10rem;
  backdrop-filter: blur(10px);
  z-index: 900;
  border-radius: 13%;
  backdrop-filter: blur(5px);
  transform: scale(1.2);
  margin-top: 40px;
  height: 120%;
  padding: 15px;
  width:80%;
  grid-column-start: 2;
  grid-column-end: 4;
  animation: vanishInWoutScale  0.5s ease 0s  normal forwards;
}
.pTagDiv:hover .hoverText{
  visibility: hidden;
}
.helloMessage{
  color: rgb(47, 118, 165);
  line-height: 0px;
  font-size: 20px;
  padding-top: 58px;
  
}

.imgTag{
  height: 600px;
  width: 600px;
}
.imgTag:active{
  animation: growShrink 3s ease 0s infinite normal none;
  
}
@keyframes growShrink{
  0%{
    transform: scale(0.75);
  }
  50%{
    transform: scale(1)
  }
  100%{
    transform: scale(0.75);
  }
}

/*===================ABOUT CSS==============================*/

.aboutContainer{
  margin-left: 5em;
}
.aboutHeader{
  font-size: 2.54em;
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start:2;
  grid-row-end: 3;
}
.aboutPTagDiv{
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start:3;
  grid-row-end: 5;
}
.aboutPTagDiv:hover{
  margin-left: 13.5em;
  background: linear-gradient(
    to bottom right,
    rgba(255, 255, 255, 0.233),
    rgba(160, 235, 252, 0.2)
  );
  border: solid rgba(255, 255, 255, 0.015) 5px;
  border-style: outset;
  border-radius: 10rem 10rem 10rem 10rem ;
  backdrop-filter: blur(10px);
  z-index: 900;
  border-radius: 13%;
  backdrop-filter: blur(5px);
  transform: scale(1.2);
  height: 120%;
  padding: 2em;
  width:80%;
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 5;
  animation: vanishInWoutScale  0.5s ease 0s  normal forwards;
}

.aboutPTagDiv:hover .hoverText{
  visibility: hidden;
}
@keyframes vanishInWoutScale {
  0% {
    opacity: 0;
    transform-origin: 50% 50%;
    /* transform: scale(1, 1); */
    filter: blur(70px);
  }
  100% {
    opacity: 1;
    transform-origin: 50% 50%;
    /* transform: scale(1.5, 1.5); */
    filter: blur(0px);
  }
}
  
