/* ================================================= */
.work:hover{
  cursor: pointer;
  border: solid 3px rgb(37, 221, 215);
  animation: vanishIn  0.5s ease 0s  normal forwards;
}
.work2:hover{
  cursor: pointer;
  border: solid 3px rgb(37, 221, 215);
  animation: vanishIn  0.5s ease 0s  normal forwards;
}
@-webkit-keyframes vanishIn {
  0% {
    opacity: 0;
    transform-origin: 50% 50%;
    transform: scale(1, 1);
    filter: blur(70px);
  }
  100% {
    opacity: 1;
    transform-origin: 50% 50%;
    transform: scale(1.5, 1.5);
    filter: blur(0px);
  }
}

@keyframes vanishIn {
  0% {
    opacity: 0;
    transform-origin: 50% 50%;
    transform: scale(1, 1);
    filter: blur(70px);
  }
  100% {
    opacity: 1;
    transform-origin: 50% 50%;
    transform: scale(1.5, 1.5);
    filter: blur(0px);
  }
}
.hiddenText{
  visibility: hidden;
  font-size:22px;
  color: #c9f2fe;
}
.workContainer:hover .hiddenText{
  position: relative;
  visibility:visible;
  animation: vanishInWoutScale  0.5s ease 0s  normal forwards;
}

/* =================CONTACT CSS=============================*/

.contact-grid-container {
  display: grid;
  grid-template-columns: 25% 50% 25% ;
  grid-template-rows: 25% 25% 25% 25%;
  grid-gap: 10px;
  padding: 10px;
  
}
.contactContainer{
  display:flex;
  justify-content:center;
  background: linear-gradient(
    to bottom right,
    rgba(255, 255, 255, 0.233),
    rgba(160, 235, 252, 0.2)
  );
  border-radius: 1.5rem;
  border-style: outset;
  border:solid 5px rgba(160, 235, 252, 0.092);
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 5;
  height: 150%;
}

.contactH1{
  margin: 0px;
  text-align: center;
  padding-bottom: 0.5em;
  text-shadow:rgba(25, 25, 25, 0.655) 3px 3px;
}

.form{
  color: rgb(128, 192, 238);
  display:grid;
  position: center;
  grid-template-columns: 50% 50% 50% 50% 50%;
  grid-template-rows: 25% 25% 25% 25% 25%;
  justify-content: center;
  font-size: 17px;
}

.name{
  grid-column-start: 3;
  grid-column-end: 4;
}

.send{
  border: solid 1px rgba(18, 235, 255, 0.473);
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 5;
  grid-row-end: 6;
  height: 50px;
  border-radius: 1em;
  background: linear-gradient(25deg,#54bdea8e , #d627c730);
}
.send:hover{
  color: rgb(151, 197, 229);
  animation: sendSpin 0.5s  infinite normal none;
}
@keyframes sendSpin{
  0%{
    transform: scale(1);
    background: linear-gradient(25deg,#54bdeac8 30%, #ad8baba5);

  }
  25%{
    
    background: linear-gradient(90deg,#54bdeac8 30%, #c45dbba5);
  }
  50%{
    transform: scale(1.05);
    background: linear-gradient(180deg,#54bdeac8 30%, #c45dbba5);
  }
  75%{
    background: linear-gradient(240deg,#54bdeac8 30%, #c45dbba5);

  }
  100%{
    transform: scale(1);
    background: linear-gradient(360deg,#54bdeac8 30%, #c45dbba5);
  }
  /* after grading coming back and re think button keyframes. its a bit strobey */
}

.githubContainer{
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 5;
  grid-row-end: 6;
}
.linkdInContainer{
  grid-column-start: 5;
  grid-column-end: 6;
  grid-row-start: 5;
  grid-row-end: 6;
}
.gitHubLogo{
  margin-left: 50%;
  height: 70px;
  filter:brightness(0);
  opacity: 70%;
  animation: logoGrowShrink 3s ease 2s infinite normal none;
}
.linkdInLogo{
  margin-right: 50%;
  height: 70px;
  opacity: 70%;
  animation: logoGrowShrink 3s ease-in-out 2s infinite normal none;
}
.gitHubLogo:hover{
  filter:brightness(50);
}
.linkdInLogo:hover{
  filter:invert()
}

  

@keyframes logoGrowShrink{
  0%{
    transform: scale(0.75);
  }
  50%{
    transform: scale(1)
  }
  100%{
    transform: scale(0.75);
  }
}
