    background-blend-mode: multiply;
    background-color: #354347;
    background-repeat: repeat;
  }
  .helloDiv{
  
    grid-column-start: 1;
    grid-column-end: 2;
    padding-top: 21px;
    margin-left:60px;
  }
  
  .h1Div{
    
    margin-left:83px;
    transform: scale(110%);
    grid-column-start: 1;
    grid-column-end: 4;
    grid-row-start: 2;
    grid-row-end: 2;
   
  }
  .pTagDiv{
    
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 3;
    grid-row-end: 4;
  }
  .imgTagDiv{ 
    margin-right: 20em;
    grid-column-start: 2;
    grid-column-end: 4;
    grid-row-start: 3;
    grid-row-end: 5;
    transform: scale(0.75)
  }
  
}

@media screen and (max-width: 768px){
  nav.navbar{
    width: 100vw;
  }
}